import React from 'react';
import Link from 'next/link';
import { inject, observer } from 'mobx-react';
import localStorage from 'mobx-localstorage';


@inject('store')
@observer

class Mob extends React.Component {
  componentDidMount() {
    console.log(this.props.store.count)
    if(localStorage.getItem('favorites')){
      this.props.store.set_movies(JSON.parse(localStorage.getItem('favorites')));
    }
  }

  componentWillUnmount() {
    this.props.store.stop()
  }

  render() {
    return (
      <div className="container">
        <center>
          <div className="row">
              {this.props.store.movielist.map((movie, index) => <Card key={index} movie={movie}/> )}
          </div>
        </center>
      </div>
    )
  }
}

export default Mob;